    import util from '../../../common/util.js';
    export default {
        
    	data() {
    		return {
    			animationData:{},
                animationTopData:{},
                animationBottomData:{},
                isActive1:false,
                isActive2:false,
                isActive3:false,
                isActive4:false,
                isActive5:false,
                isActive6:false,
                isActive7:false,
                isActive8:false,
                isActive9:false,
                name:'',
                sex:2,
                age:null,
                tel:'',
                weight:null,
                height:null,
                boxHeight:100,
                type:0
    		}
    	},
        onShow() {
            this.startAnimation()
        },
    	onLoad(options) {
            this.type = options.type
            if (this.type == 2) uni.setNavigationBarTitle({title: '脏腑辨证'})
            var alInfo = uni.getStorageSync('userBaseInfo');
   
            if(alInfo){
                this.name = alInfo.name
                if(alInfo.gender == 'MAN'){
                    this.sex = 0
                }else{
                    this.sex = 1
                }
                
                this.age = alInfo.age
                this.tel = alInfo.phone
                this.weight = alInfo.weight
                this.height = alInfo.height
            }
    	},
        methods: {
            clickSex:function(sex){
                this.sex = sex
            },
            clickCancel:function(){
                uni.navigateBack({
                    delta:1
                })
            },
            clickConfirm:function(){
                if(this.name == ''){
                    uni.showToast({
                        title: '请输入姓名',
                        icon: "none",
                        duration: 2000
                    })
                    return;
                }
                if(this.sex == 2){
                    uni.showToast({
                        title: '请选择性别',
                        icon: "none",
                        duration: 2000
                    })
                    return;
                }
                console.log(this.age)
                if(!this.age){
                    uni.showToast({
                        title: '请输入年龄',
                        icon: "none",
                        duration: 2000
                    })
                    return;
                }
                // if(this.tel == ''){
                //     uni.showToast({
                //         title: '请输入电话',
                //         icon: "none",
                //         duration: 2000
                //     })
                //     return;
                // }
                // if (!util.verifyMobile(this.tel)) {
                //     uni.showToast({
                //         title: '请输入正确的手机号码',
                //         icon: "none",
                //         duration: 2000
                //     })
                //     return;
                // }
                if(!this.height ){
                    uni.showToast({
                        title: '请输入身高',
                        icon: "none",
                        duration: 2000
                    })
                    return;
                }
                if(!this.weight ){
                    uni.showToast({
                        title: '请输入体重',
                        icon: "none",
                        duration: 2000
                    })
                    return;
                }
                if(this.height > 300){
                    uni.showToast({
                        title: '身高不能大于300cm',
                        icon: "none",
                        duration: 2000
                    })
                    return;
                }
                if(this.weight >250){
                    uni.showToast({
                        title: '体重不能大于250kg',
                        icon: "none",
                        duration: 2000
                    })
                    return;
                }
                var gender
                if(this.sex == 0){
                    gender = 'MAN'
                }else{
                    gender = 'LADY'
                }
                
                var alInfosss = uni.getStorageSync('AlInfo');
                if(alInfosss && alInfosss.gender == gender){
                    alInfosss.name = this.name
                    alInfosss.age = this.age
                    alInfosss.phone = this.tel
                    alInfosss.height = this.height
                    alInfosss.weight = this.weight
                    uni.setStorageSync('AlInfo', alInfosss)
					uni.setStorageSync('userBaseInfo', alInfosss)
                }else{
                    var alInfo = {
                        "age": this.age,
                        "gender": gender,
                        "name": this.name,
                        "phone": this.tel,
                        "weight":this.weight,
                        "height":this.height
                    }
                    // console.log(alInfo)
                    // alInfo = {"age":"44","gender":"MAN","name":"we","phone":"18007770666","tongueImages":{"body":"http://coffer.bainuo.cn/useravatar-pub/cf/cfdeaf874723f4ee066ba97f758c10c8.jpg","front":"http://coffer.bainuo.cn//useravatar-pub/35/35d0efbfe64c1df9db19a5f5715bcfd0.png","back":"http://coffer.bainuo.cn//useravatar-pub/35/35d0efbfe64c1df9db19a5f5715bcfd0.png","flag": false,}}
                    uni.setStorageSync('AlInfo', alInfo)
					uni.setStorageSync('userBaseInfo', alInfo)
                }
               
                if(this.type == 2){
                    uni.navigateTo({
                        url:'healthInfo?type='+this.type
                    })
                }else{
                    uni.navigateBack({
                        delta:1
                    })
                }
                
            },
            startAnimation:function(){
                var animation = uni.createAnimation({
                    duration: 500,
                    timingFunction: 'ease',
                })
                var animationTop = uni.createAnimation({
                    duration: 500,
                    timingFunction: 'ease',
                })
                var animationBottom = uni.createAnimation({
                    duration: 500,
                    timingFunction: 'ease',
                })
                var yyy = uni.upx2px(100*9)/2 - 4
                console.log(yyy)
                setTimeout(function() {
                    animation.scale(1, 10).step()
                    this.animationData = animation.export()
                    animationTop.translate(0,-yyy).step()
                    this.animationTopData = animationTop.export()
                    animationBottom.translate(0,yyy).step()
                    this.animationBottomData = animationBottom.export()
                }.bind(this), 300)
                var _self = this
                setTimeout(function() {
                    this.isActive4 = true
                    setTimeout(function() {
                        this.isActive3 = true
                        this.isActive5 = true
                        setTimeout(function() {
                            this.isActive2 = true
                            this.isActive6 = true
                            setTimeout(function() {
                                this.isActive1 = true
                                this.isActive7 = true
                                this.isActive8 = true
                                this.isActive9 = true
                                this.$nextTick(function() {
                                    
                                    uni.createSelectorQuery().select(".box").fields({
                                        size: true,
                                        scrollOffset: true
                                    }, (data) => {
                                        console.log(data);
                                        _self.boxHeight = data.height - 10
                                    }).exec();
                                })
                            }.bind(this), 200)
                        }.bind(this), 200)
                    }.bind(this), 200)
                }.bind(this), 200)
            }
        }
    }
